html{
  box-sizing: border-box;
  padding: 0;
  margin:0;
  font-size: 16px;
  font-family: serif;
  background-color: #222222cb;
  color:#c0c0c0;
}

*after,
*before{
box-sizing: inherit;
}

input[type="text"]{
border: thin solid #feee;
border-radius: 0.25rem;
padding: 0.75rem;
margin-bottom: 1rem;
outline: none;
display: block;
width: 100%;
font-size: 1rem;
line-height: 1;
background-color: transparent;
};

button,
input[type="submit"],
input[type="reset"]{
  border: thin solid #444;
  border-radius: 0.25rem;
padding: 0.75rem;
margin-bottom: 1rem;
display: inline-block;
background-color: #feee;
color: #444;
font-weight: bold;
font-size: 1rem;
line-height: 1;
text-transform: none;
text-decoration: none;
text-align: center;
vertical-align: middle;
cursor: pointer;
}

.slick-track{
  width:100% !important;
}

hr{
  margin: 2rem auto;
}

table{
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

th,
td{
  text-align: left;
  padding: 0.5rem;
  border-bottom: thin solid #dedede;
}

